.LayoutHeader {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-top-right-radius: 0.375rem;
      --tw-bg-opacity: 1;
      background-color: rgb(51 51 51 / var(--tw-bg-opacity));
      padding: 1rem;
      --tw-text-opacity: 1;
      color: rgb(255 255 255 / var(--tw-text-opacity));
}

@media (min-width: 768px) {

      .LayoutHeader {
            padding-right: 1.25rem;
      }
}

@media (min-width: 1024px) {

      .LayoutHeader {
            padding-left: 2.75rem;
      }
}

@media (min-width: 1280px) {

      .LayoutHeader {
            padding-left: 3rem;
      }
}

  .LayoutHeader--closed {
      border-bottom-right-radius: 0.375rem;
}

  .LayoutHeader__Infos {
      display: flex;
      align-items: center;
      gap: 0.75rem;
}

  @media (min-width: 1280px) {

      .LayoutHeader__Infos {
            gap: 1.75rem;
      }
}

  .LayoutHeader__Title {
      font-size: 1rem;
      line-height: 1.5rem;
      font-weight: 800;
}

  .LayoutHeader__Actions {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      gap: 0.75rem;
}

  .LayoutHeader__Collapse {
      display: flex;
      align-items: stretch;
}

  .LayoutHeader__Collapse:hover .LayoutHeader__Collapse__Label {
      --tw-bg-opacity: 1;
      background-color: rgb(250 83 60 / var(--tw-bg-opacity));
}

  .LayoutHeader__Collapse__Icon {
      border-top-left-radius: 0.375rem;
      border-bottom-left-radius: 0.375rem;
      --tw-bg-opacity: 1;
      background-color: rgb(250 83 60 / var(--tw-bg-opacity));
      padding: 0.5rem;
}

  .LayoutHeader__Collapse__Label {
      border-top-right-radius: 0.375rem;
      border-bottom-right-radius: 0.375rem;
      --tw-bg-opacity: 1;
      background-color: rgb(220 48 24 / var(--tw-bg-opacity));
      padding: 0.5rem;
      font-size: 0.75rem;
      line-height: 1rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.05em;
}

  .LayoutHeader__DeleteButton {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 0.375rem;
      --tw-bg-opacity: 1;
      background-color: rgb(220 48 24 / var(--tw-bg-opacity));
      padding: 0.5rem;
}

  .LayoutHeader__DeleteButton > svg {
      height: 16px;
      width: 16px;
    }

  .LayoutHeader__DeleteButton:hover {
      --tw-bg-opacity: 1;
      background-color: rgb(250 83 60 / var(--tw-bg-opacity));
}