.BlockAccordion {
  display: flex;
  flex-direction: column;
  border-radius: 0.375rem;
  border-left-width: 8px;
  --tw-border-opacity: 1;
  border-left-color: rgb(220 48 24 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25)
}

  .BlockAccordion__Content {
  display: flex;
  flex-direction: column;
  gap: 1.5rem
}

  .BlockAccordion--closed {
  display: none
}

  .BlockAccordion__Add {
  width: max-content;
  border-radius: 0.375rem;
  border-width: 2px;
  --tw-border-opacity: 1;
  border-color: rgb(220 48 24 / var(--tw-border-opacity));
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  font-size: 0.75rem;
  line-height: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  --tw-text-opacity: 1;
  color: rgb(220 48 24 / var(--tw-text-opacity))
}

  .BlockAccordion__Add:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(220 48 24 / var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity))
}