.BlockTable {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

  .BlockTable-rowsCol,
  .BlockTable .Input__Wrapper {
    flex-grow: 1;
    flex-shrink: 0;
  }

  .BlockTable .Title {
  width: 100%;
  font-weight: 700;
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity));
}

  @media (min-width: 768px) {

  .BlockTable .Title {
    font-size: 1.25rem;
    line-height: 1.75rem;
  }
}

  .BlockTable .Title--2 {
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 600;
  text-transform: uppercase;
}

  .BlockTable-layout, 
  .BlockTable-rowsList, 
  .BlockTable-headers {
  flex-wrap: wrap;
  border-radius: 0.375rem;
  border-left-width: 8px;
  --tw-border-opacity: 1;
  border-color: rgb(220 48 24 / var(--tw-border-opacity));
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

  @media (min-width: 1024px) {

  .BlockTable-layout, 
  .BlockTable-rowsList, 
  .BlockTable-headers {
    padding-left: 3.5rem;
    padding-right: 3.5rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
}

  .BlockTable-layout,
  .BlockTable-rowsList,
  .BlockTable-headers {
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  }

  .BlockTable-layout .Title, .BlockTable-rowsList .Title, .BlockTable-headers .Title {
      width: 100%;
    }

  .BlockTable-layout {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }

  .BlockTable-headers{
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .BlockTable-headersList {
      display: flex;
      gap: 16px;
    }

  .BlockTable-headersListItem {
        flex-grow: 1;
        flex-shrink: 0;
      }

  .BlockTable-headersListItem + .BlockTable-headersListItem {
        border-left: 1px solid #d1d5db;
        padding-left: 16px;
      }

  .BlockTable-headersList .Input__Wrapper label {
  width: 100%;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 600;
  text-transform: uppercase;
  --tw-text-opacity: 1;
  color: rgb(51 51 51 / var(--tw-text-opacity));
}

  .BlockTable-rows{
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .BlockTable-rowsWrapper {
      display: flex;
      flex-direction: column;
      gap: 24px;
    }

  .BlockTable-rowsList {
      display: flex;
      flex-wrap: wrap;
      gap: 16px;
    }

  .BlockTable-rowsCol + .BlockTable-rowsCol {
      border-left: 1px solid #d1d5db;
      padding-left: 16px;
    }

  .BlockTable-rowsCol {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      gap: 16px;
    }

  .BlockTable-rowsCol .Input__Wrapper {
        flex-grow: 0;
      }